@use 'base/mixins' as m;
@use 'base/variables' as v;

.header{
    padding: 5rem 0;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    @include m.tablet {
        min-height: 50rem;
    }
    &::before{
        content: '';
        background-color: v.$black;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: .5;
    }

    .header__content{
        position: relative;
        h1{
            text-align: center;
            color: v.$white;
            text-transform: uppercase;
            margin-top: 5rem;
        }
        @include m.tablet {
            h1{
                margin-top: 10rem;
            }
        }
    }
}

.header__home{
    background-image: url(../img/headerHome.jpg);
    background-position: center bottom;
}

.header__about{
    background-image: url(../img/HeadeAbout.jpg);
    background-position: center bottom;
}

.header__process{
    background-image: url(../img/headerProcess.jpg);
    background-position: center center;
}

.header__menu{
    background-image: url(../img/headerMenu.jpg);
    background-position: center center;
}

.header__gallery{
    background-image: url(../img/headerGallery.jpg);
    background-position: center center;
}

.header__contact{
    background-image: url(../img/headerContact.jpg);
    background-position: center center;
}