@use 'base/mixins' as m;
@use 'base/variables' as v;

.navbar{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    margin-top: 5rem;

    @include m.tablet {
        flex-direction: row;
        margin-top: 0;
    }
    a{
        color: v.$white;
        font-size: 2rem;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 1rem;

        &:hover{
            transition: 0.3s;
            color: v.$secondary;
        }
        &.active{
            color: v.$secondary;
        }
    }
    @include m.tablet {
        a{
            margin-bottom: 0;
        }
    }

}