@use 'base/mixins' as m;
@use 'base/variables' as v;

.process__coffe{
    margin-bottom: 2rem;

    @include m.tablet {
        @include m.grid(2, 2rem) ;
    }
    
    &:last-of-type{
        margin-bottom: 0;
    }

    .process_image{
        height: auto;

    }

    .process__text{
        margin-top: 1rem;
        h3{
            margin: 0;
            text-align: left;
            color: v.$primary;

            &::after {
                content: '';
                display: block;
                width: 50%;
                height: 5px;
                background-color: v.$secondary ;
            }
        }
    }
}