@use 'base/mixins' as m;
@use 'base/variables' as v;

.menu__content{
    background-image: url(../img/coffe.jpg);
    background-repeat: no-repeat;
    background-position: center bottom ;
    background-size: 100%;
    aspect-ratio: 3/6 ;

    @include m.phone {
        aspect-ratio: 3/3;
        background-size: 80%
    }

    @include m.tablet {
        @include m.grid(2, 5rem) ;
        background-size: 50%;
        aspect-ratio: 16/6;
        background-position: bottom right;
    }

    .menu__text{
        p{
            
            font-size: 2rem;
        }
    }
}