@use 'base/mixins' as m;
@use 'base/variables' as v;

.footer{
    background-color: v.$primary;
    .footer__grid{
        background-color: v.$white;
        padding: 5rem;

        @include m.tablet {
            @include m.grid(3, 2rem)
            
        }

    }
    .copyright{
        text-align: center;
        margin: 0;
        color: v.$white;
        padding: 2rem;
        font-size: 2.5rem;
    }
}

.footer__box{
    font-family: v.$secondary_font;
    text-align: center;
    margin-bottom: 5rem;

    @include m.tablet {
        margin-bottom: 0;
    }
    h3{
        margin: 0 0 1rem 0;
        font-size: 2.8rem;
        color: v.$secondary;
    }

    p,a {
        font-size: 2rem;
        margin: 1rem;
    }
}