@use 'base/variables' as v;

h1,h2,h3{
    font-family: v.$principal_font;
    margin: 0 0 5rem 0;
    text-transform: uppercase;
    line-height: 1.5;
    text-align: center;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
    color: v.$primary;
}
.main__title{
    color: v.$secondary;
    font-size: 1.8rem;

    &::after{
        content: '';
        display: block;
        width: 11rem;
        height: 11rem;
        margin: 0 auto;
        background-image: url(../img/coffe.svg);
        background-size: 10rem;
        background-repeat: no-repeat;
    }

}
.main__title-span{
    font-size: 3.6rem;
    color: v.$primary;
    display: block;
}

h3{
    font-size: 3rem;
}
