@use 'base/mixins' as m;
@use 'base/variables' as v;

.menues{
    background-image: url(../img/menu2.svg), url(../img/menu1.svg), linear-gradient(to top, v.$primary, v.$primary );
    background-repeat: no-repeat;
    background-position: center 5rem, center 92%, 100%;
    background-size: 200%;
    padding: 15rem 5rem 20rem 2rem;

    @include m.tablet {
        padding: 15rem 0 20rem 0;
        background-size: 100%;
    }
    @include m.desktop {
        background-size: 50%, 50%, 100%
    }

    h2{
        color: v.$white;
        font-size: 1.8rem;
        span{
            color:v.$white; 
            display: block;
            font-size: 4.5rem;      
        }
    }
}

.menu__grid{
    @include m.tablet {
        @include m.grid(2, 2rem)
    }
}

.menu{
    li{
        display: flex;
        justify-content: space-between;
        align-items: center;
        p{
            color: v.$white;
            font-size: 2.2rem;
            line-height: 1.5;
            margin: 0 0 2rem 0;
            span{
                display: block;
                color: v.$secondary;
                font-size: 1.8rem;

            }
        }
        .precio{
            font-size: 4rem;
            font-weight: 700;

        }
    }

    h3{
        color: v.$white;
    }
}