@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonials{
    padding: 10rem 2rem;
    margin-top: -8rem;
    background-color: v.$white;

    @include m.tablet {
        padding: 10rem;
    }
    .testimonial{
        background-color: v.$ligthGray;
        padding: 2rem 4rem;
        margin: 0 auto;
        border-radius: 2rem;

        @include m.tablet {
            width: 50%;
        }


        .author{
            text-align: right;
            font-size: 2.4rem;
            color: v.$primary;
            font-family: v.$tertiary_font;

        }
    }
}