@use 'variables' as v;

html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,*:before,*:after{
    box-sizing: inherit;
}

body{
    font-family: v.$secondary_font;
    font-size: 1.6rem;
    background-color: v.$lightBrown;

    &.fix-body{
        overflow: hidden;
    }
/*     &.body-scroll{
        padding-top: 8.6rem;
    } */
}

p{
    color: v.$black;
    font-size: 1.6rem;
    line-height: 2;
}

a{
    text-decoration: none;
    color: v.$black;
    transition: 0.3s;

    &:hover{
        color: v.$secondary;
    }
}

img{
    max-width: 100%;
    height: auto;
    display: block;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.container{
    width: min(90%, 120rem);
    margin: 0 auto;
}