@use 'base/mixins' as m;
@use 'base/variables' as v;


.header__bar{
    display: flex;
    flex-direction: column;

    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .header__logo{
        width: 50%;
        margin: 0 auto;
    }

    @include m.tablet {
        .header__logo{
            width: 15rem;
            margin: 0;
        }
    }

}