@use 'base/variables' as v;
@use 'base/mixins' as m;

.icons{
    margin-top: 5rem;
    .icon__list{
        @include m.tablet { 
            @include m.grid(3, 2rem);
        }

        li {
            img {
                margin: 0 auto;
                width: 15rem;
            }
            p {
                font-family: v.$tertiary_font;
                font-size: 2.8rem;
                color: v.$primary;
            }
        }
    }
        
}

