@use 'base/mixins' as m;
@use 'base/variables' as v;

.gallery{
    @include m.grid(2, 1rem);
    &:nth-child(2){
        picture,img{
            object-position: right;
        }
    }
    @include m.desktop {
        @include m.grid(4, 2rem);
    }
    .gallery__image{
        picture,img{
            width: 30rem;
            height: 30rem;
            object-fit: cover;
        }
        @include m.desktop {
            &:nth-child(1){
                grid-row: 1/3;

                picture,img{
                    height: 62rem;
                }
            }
            &:nth-child(2){
                grid-column: 2/4;

                picture,img{
                    width: 62rem;
                    object-position: bottom;
                }
            }
            &:nth-child(4){
                grid-row: 2/4;

                picture,img{
                    height: 62rem;
                    object-position: bottom;
                }
            }
            &:nth-child(5){
                grid-column: 3/5;
                picture,img{
                    width: 62rem;
                    object-position: bottom center;
                }
            }
            &:nth-child(6){
                grid-column: 3/5;

                picture,img{
                    width: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(7){
                grid-row: 3/5;

                picture,img{
                    height:62rem;
                }
            }
            &:nth-child(8){
                grid-column: 2/5;
            }
            picture,img{
                width:94rem;
            }
            &:nth-child(9){
                grid-column: 1/3;
                picture,img{
                    object-position:center 60%;
                }
            }
            &:nth-child(10){
                grid-column: 3/6;
                picture,img{
                    object-position: center 70%;
                }
            }

    }
} 
}