@use 'base/mixins' as m;
@use 'base/variables' as v;

.contact__content{
    @include m.tablet {
        @include m.grid(2, 2rem);
        background-image: url(../img/grains.jpg);
        background-repeat: no-repeat;
        background-position: right center;
        background-size: contain;

    }
}
.form{
    .field{
        margin-bottom: 2rem;

        &:last-of-type{
            margin-bottom: 0;
        }
        label{
            text-transform: uppercase;
            display: block;
            font-size: 2.2rem;
            font-weight: 700;
        }
        input:not([type='submit']),
        select{
            width: 100%;
            padding: 1rem;
            border: .5rem solid v.$primary;

        }

        input[type='submit']{
            background-color: v.$primary;
            width: 100%;
            border: none;
            padding: 1rem;
            color: v.$white;
            font-size: 2.2rem;
            font-weight: 700;
            text-transform: uppercase;

            &:hover{
                transition: 0.3s;
                transform: scale(.9);
                border-radius: 1rem;
                cursor: pointer;
            }
        }
    }
}