body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #ebe7de;
  /*     &.body-scroll{
          padding-top: 8.6rem;
      } */
}
body.fix-body {
  overflow: hidden;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

a {
  text-decoration: none;
  color: #000000;
  transition: 0.3s;
}
a:hover {
  color: #C39C63;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  width: min(90%, 120rem);
  margin: 0 auto;
}

h1, h2, h3 {
  font-family: "Anek Latin", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  line-height: 1.5;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  color: #5E311E;
}

.main__title {
  color: #C39C63;
  font-size: 1.8rem;
}
.main__title::after {
  content: "";
  display: block;
  width: 11rem;
  height: 11rem;
  margin: 0 auto;
  background-image: url(../img/coffe.svg);
  background-size: 10rem;
  background-repeat: no-repeat;
}

.main__title-span {
  font-size: 3.6rem;
  color: #5E311E;
  display: block;
}

h3 {
  font-size: 3rem;
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 50rem;
  }
}
.header::before {
  content: "";
  background-color: #000000;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.5;
}
.header .header__content {
  position: relative;
}
.header .header__content h1 {
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .header__content h1 {
    margin-top: 10rem;
  }
}

.header__home {
  background-image: url(../img/headerHome.jpg);
  background-position: center bottom;
}

.header__about {
  background-image: url(../img/HeadeAbout.jpg);
  background-position: center bottom;
}

.header__process {
  background-image: url(../img/headerProcess.jpg);
  background-position: center center;
}

.header__menu {
  background-image: url(../img/headerMenu.jpg);
  background-position: center center;
}

.header__gallery {
  background-image: url(../img/headerGallery.jpg);
  background-position: center center;
}

.header__contact {
  background-image: url(../img/headerContact.jpg);
  background-position: center center;
}

.navbar {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .navbar {
    flex-direction: row;
    margin-top: 0;
  }
}
.navbar a {
  color: #FFFFFF;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
.navbar a:hover {
  transition: 0.3s;
  color: #C39C63;
}
.navbar a.active {
  color: #C39C63;
}
@media (min-width: 768px) {
  .navbar a {
    margin-bottom: 0;
  }
}

.header__bar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .header__bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__bar .header__logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__bar .header__logo {
    width: 15rem;
    margin: 0;
  }
}

.main__content {
  background-color: #FFFFFF;
  padding: 5rem 2rem;
  position: relative;
  margin-top: -10rem;
}
@media (min-width: 768px) {
  .main__content .main__content {
    padding: 10rem;
  }
}

.icons {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .icons .icon__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.icons .icon__list li img {
  margin: 0 auto;
  width: 15rem;
}
.icons .icon__list li p {
  font-family: "Playfair Display", serif;
  font-size: 2.8rem;
  color: #5E311E;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.gallery:nth-child(2) picture, .gallery:nth-child(2) img {
  object-position: right;
}
@media (min-width: 992px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.gallery .gallery__image picture, .gallery .gallery__image img {
  width: 30rem;
  height: 30rem;
  object-fit: cover;
}
@media (min-width: 992px) {
  .gallery .gallery__image:nth-child(1) {
    grid-row: 1/3;
  }
  .gallery .gallery__image:nth-child(1) picture, .gallery .gallery__image:nth-child(1) img {
    height: 62rem;
  }
  .gallery .gallery__image:nth-child(2) {
    grid-column: 2/4;
  }
  .gallery .gallery__image:nth-child(2) picture, .gallery .gallery__image:nth-child(2) img {
    width: 62rem;
    object-position: bottom;
  }
  .gallery .gallery__image:nth-child(4) {
    grid-row: 2/4;
  }
  .gallery .gallery__image:nth-child(4) picture, .gallery .gallery__image:nth-child(4) img {
    height: 62rem;
    object-position: bottom;
  }
  .gallery .gallery__image:nth-child(5) {
    grid-column: 3/5;
  }
  .gallery .gallery__image:nth-child(5) picture, .gallery .gallery__image:nth-child(5) img {
    width: 62rem;
    object-position: bottom center;
  }
  .gallery .gallery__image:nth-child(6) {
    grid-column: 3/5;
  }
  .gallery .gallery__image:nth-child(6) picture, .gallery .gallery__image:nth-child(6) img {
    width: 62rem;
    object-position: center;
  }
  .gallery .gallery__image:nth-child(7) {
    grid-row: 3/5;
  }
  .gallery .gallery__image:nth-child(7) picture, .gallery .gallery__image:nth-child(7) img {
    height: 62rem;
  }
  .gallery .gallery__image:nth-child(8) {
    grid-column: 2/5;
  }
  .gallery .gallery__image picture, .gallery .gallery__image img {
    width: 94rem;
  }
  .gallery .gallery__image:nth-child(9) {
    grid-column: 1/3;
  }
  .gallery .gallery__image:nth-child(9) picture, .gallery .gallery__image:nth-child(9) img {
    object-position: center 60%;
  }
  .gallery .gallery__image:nth-child(10) {
    grid-column: 3/6;
  }
  .gallery .gallery__image:nth-child(10) picture, .gallery .gallery__image:nth-child(10) img {
    object-position: center 70%;
  }
}

.menues {
  background-image: url(../img/menu2.svg), url(../img/menu1.svg), linear-gradient(to top, #5E311E, #5E311E);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%;
  background-size: 200%;
  padding: 15rem 5rem 20rem 2rem;
}
@media (min-width: 768px) {
  .menues {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
}
@media (min-width: 992px) {
  .menues {
    background-size: 50%, 50%, 100%;
  }
}
.menues h2 {
  color: #FFFFFF;
  font-size: 1.8rem;
}
.menues h2 span {
  color: #FFFFFF;
  display: block;
  font-size: 4.5rem;
}

@media (min-width: 768px) {
  .menu__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.menu li {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu li p {
  color: #FFFFFF;
  font-size: 2.2rem;
  line-height: 1.5;
  margin: 0 0 2rem 0;
}
.menu li p span {
  display: block;
  color: #C39C63;
  font-size: 1.8rem;
}
.menu li .precio {
  font-size: 4rem;
  font-weight: 700;
}
.menu h3 {
  color: #FFFFFF;
}

.testimonials {
  padding: 10rem 2rem;
  margin-top: -8rem;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .testimonials {
    padding: 10rem;
  }
}
.testimonials .testimonial {
  background-color: #eceeef;
  padding: 2rem 4rem;
  margin: 0 auto;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .testimonials .testimonial {
    width: 50%;
  }
}
.testimonials .testimonial .author {
  text-align: right;
  font-size: 2.4rem;
  color: #5E311E;
  font-family: "Playfair Display", serif;
}

.footer {
  background-color: #5E311E;
}
.footer .footer__grid {
  background-color: #FFFFFF;
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer .copyright {
  text-align: center;
  margin: 0;
  color: #FFFFFF;
  padding: 2rem;
  font-size: 2.5rem;
}

.footer__box {
  font-family: "Roboto", sans-serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer__box {
    margin-bottom: 0;
  }
}
.footer__box h3 {
  margin: 0 0 1rem 0;
  font-size: 2.8rem;
  color: #C39C63;
}
.footer__box p, .footer__box a {
  font-size: 2rem;
  margin: 1rem;
}

@media (min-width: 768px) {
  .about__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
.about__content .about__image {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .about__content .about__image {
    margin: 0 2rem 0 0;
  }
}
.about__content .about__image img {
  transform: rotate(-6deg);
  border: 3rem solid #FFFFFF;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
}
.about__content .about__text blockquote {
  text-align: left;
  margin: 0;
  padding: 1rem;
  border-left: 0.5rem solid #5E311E;
  font-size: 2rem;
}

.process__coffe {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .process__coffe {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.process__coffe:last-of-type {
  margin-bottom: 0;
}
.process__coffe .process_image {
  height: auto;
}
.process__coffe .process__text {
  margin-top: 1rem;
}
.process__coffe .process__text h3 {
  margin: 0;
  text-align: left;
  color: #5E311E;
}
.process__coffe .process__text h3::after {
  content: "";
  display: block;
  width: 50%;
  height: 5px;
  background-color: #C39C63;
}

.menu__content {
  background-image: url(../img/coffe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/6;
}
@media (min-width: 480px) {
  .menu__content {
    aspect-ratio: 3/3;
    background-size: 80%;
  }
}
@media (min-width: 768px) {
  .menu__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-size: 50%;
    aspect-ratio: 16/6;
    background-position: bottom right;
  }
}
.menu__content .menu__text p {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contact__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    background-image: url(../img/grains.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: contain;
  }
}

.form .field {
  margin-bottom: 2rem;
}
.form .field:last-of-type {
  margin-bottom: 0;
}
.form .field label {
  text-transform: uppercase;
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
}
.form .field input:not([type=submit]),
.form .field select {
  width: 100%;
  padding: 1rem;
  border: 0.5rem solid #5E311E;
}
.form .field input[type=submit] {
  background-color: #5E311E;
  width: 100%;
  border: none;
  padding: 1rem;
  color: #FFFFFF;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.form .field input[type=submit]:hover {
  transition: 0.3s;
  transform: scale(0.9);
  border-radius: 1rem;
  cursor: pointer;
}

.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #5E311E;
}

.padding-0 {
  padding: 0;
}